/* #331c5d */


#header {
  top: 0;
  position: fixed;
  min-width: 100vw;
  max-width: 100vw;
  height: 64px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  overflow-y: visible;
  z-index: 12;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

#header.collapsed {
  height: 416px;
}

#header-logo-container, #header-button-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: row;
}

#header-logo-container {
  overflow-y: visible;
}

.header-logo-sub-container .header-logo-padding-top {
  height: calc(100% - 64px);
  background-color: #331c5d;
}

#header-logo {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  position: relative;
  align-self: flex-start;
  z-index: 12;
  /* filter: drop-shadow(0 3px 3px rgba(0, 0, 0, 0.7)); */
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
  height: 120px;
}

#header-first {
  width: calc(8% - 8px);
}

#header-last {
  flex-grow: 1;
}

.header-shadow-clear {
  padding-left: 2px;
  padding-right: 2px;
  width: 8px;
  z-index: 16;
}

#header-first, #header-last, .header-shadow-clear {
  box-sizing: border-box;
  height: 100%;
  background-color: #331c5d;
}

#header-first {
  -webkit-filter: drop-shadow(-5px 3px 3px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(-5px 3px 3px rgba(0, 0, 0, 0.7));
}

#header-last {
  -webkit-filter: drop-shadow(5px 3px 3px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(5px 3px 3px rgba(0, 0, 0, 0.7));
}

#header-first, #header-last {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  z-index: 13;
}

#header-button-container {
  z-index: 19;
  justify-content: flex-end;
  align-items: center;
}

#header-button-container a, .header-button {
  text-decoration: none;
}


.header-button {
  font-family: 'Oswald', sans-serif;
  cursor: pointer;
  text-align: center;
  width: 96px;
  color: white;
  line-height: 64px;
}

.header-button.active {
  background-color: #7a0056;
}

#header-hamburger-container {
  margin-top: 20px;
  margin-left: 32px;
  margin-right: 32px;
  width: 36px;
  height: 24px;
  position: absolute;
  right: 0;
  align-self: flex-start;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 20;
  display: none;
}

#header-hamburger-container span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background-color: #7a0056;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

#header-hamburger-container span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

#header-hamburger-container span:nth-child(2) {
  top: 10px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

#header-hamburger-container span:nth-child(3) {
  top: 20px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}

#header-hamburger-container.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -3px;
  left: 0px;
}

#header-hamburger-container.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#header-hamburger-container.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 23px;
  left: 0px;
}

@media only screen and (max-width: 1170px) {
  .header-button:not(.first) {
    border-left: 1px solid #7a0056;
  }

  #header {
    transition: 0.3s;
  }

  #header-hamburger-container {
    display: block;
  }

  #header:not(.collapsed) .header-button {
    display: none;
  }

  #header-button-container {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 64px;
    overflow: hidden;
  }

  #header-button-container .header-button {
    width: 100vw;
    height: 64px;
    border-left: 0;
  }

  #header-button-container .header-button {
    border-bottom: 1px solid #7a0056;
  }

  .header-media-container {
    text-align: center;
    width: 100%;
    align-self: center;
    padding-top: 12px;
  }

  .header-media-container .media-button-container {
    margin-top: 24px;
    float: none;
  }
}

@media only screen and (min-width: 1170px) {
  .header-button {
    border-left: 1px solid #7a0056;
  }

  .header-button.last {
    padding-right: 20px;
  }

}

